  font-weight: bold;
  font-size: 11px;
  color: var(--color-grey);
}

.menu__list-link:hover {
  border-bottom: var(--border-link-hover) solid var(--color-primary);
  padding-top: var(--border-link-hover);
  color: var(--color-black);
}

